import React, { useEffect } from 'react';
import { MainTagLayout, RootLayout } from 'components/Layouts/AppLayouts';
import ObserversList from 'business/Observers/List';
import EventsList from 'business/Events/List';
import ProductsProvider from 'contexts/Products';

import Hero from './Hero';
import { useSubscription } from '@apollo/client';
import { ALERT_CHANGED } from 'business/Events/List/subscriptions';
import { useSnackbar } from 'contexts/Snackbar';
import Footer from 'components/Footer';
import Header from './Header';

const Home = () => {
  const { info } = useSnackbar();
  const { data } = useSubscription(ALERT_CHANGED);

  const onSubscriptionChanged = ({ alertChanged }) =>
    info(`Alert #${alertChanged.id} has been triggered.`);

  useEffect(() => {
    if (data) {
      onSubscriptionChanged(data);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [data]);

  return (
    <ProductsProvider>
      <Header />
      <Hero />
      <RootLayout>
        <MainTagLayout>
          <ObserversList />
          <EventsList />
        </MainTagLayout>
      </RootLayout>
      <Footer />
    </ProductsProvider>
  );
};

export default Home;
