import { useMutation } from '@apollo/client';
import styled from '@emotion/styled';
import IconButton from 'components/Buttons/IconButton';
import Icon from 'components/Icon';
import Tooltip from 'components/Tooltip';
import dayjs from 'dayjs';
import React from 'react';
import useScreenType from 'theme/useScreenType';
import ucfirst from 'utils/ucfirst';
import { DELETE_ALERT } from './mutations';

const OrderBlockContainer = styled.div`
  height: 100%;
  padding: 6px;
  border-radius: 3px;
  border: 1px solid rgba(0, 0, 0, 0.125);
`;

const OrderText = styled.strong`
  color: ${({ theme, default: _default }) =>
    _default ? theme.default : theme.errorDarken};
`;

const OrderBlock = ({ status, orderOptions, info }) => (
  <React.Fragment>
    <OrderBlockContainer>
      {status !== 'executed' && (
        <React.Fragment>
          [<OrderText default>{orderOptions.symbol}</OrderText>]{' '}
          <OrderText default>{orderOptions.side}</OrderText>,{' '}
          <OrderText>{orderOptions.type}</OrderText>
          {orderOptions.type === 'LIMIT' && (
            <OrderText>{`: ${orderOptions.price} (ba)`}</OrderText>
          )}
          , quantity:
          <OrderText> {orderOptions.quantity}</OrderText>
        </React.Fragment>
      )}
      {status === 'executed' && (
        <React.Fragment>
          [<OrderText default>{info.symbol}</OrderText>]{' '}
          <OrderText default>{info.side}</OrderText>,{' '}
          <OrderText>{info.type}</OrderText>
          {info.type === 'LIMIT' && (
            <OrderText>{`: ${info.price} (ba)`}</OrderText>
          )}
          , quantity:
          <OrderText> {info.quantity}</OrderText>,{' '}
          <a
            rel='noreferrer'
            target='_blank'
            href='https://www.binance.com/en/my/orders/exchange/openorder'
          >
            {info.orderId}
          </a>
        </React.Fragment>
      )}
    </OrderBlockContainer>
    {status === 'failed' && (
      <OrderBlockContainer>
        <OrderText>{info.error}</OrderText>
      </OrderBlockContainer>
    )}
  </React.Fragment>
);

const CardContainer = styled.article`
  padding: 12px;
  &:not(:last-child) {
    border-bottom: 1px solid rgba(0, 0, 0, 0.125);
  }
  & .card-actions {
    display: block;
  }
  &:hover .card-actions {
    display: none;
  }
  & .card-delete {
    display: none;
  }
  &:hover .card-delete {
    display: block;
  }
`;

const FlexCardContainer = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
`;

const FlexInfoContainer = styled.div`
  display: flex;
  align-items: center;
  & > *:not(:last-child) {
    margin-right: 12px;
  }
`;

const EventType = styled.div`
  padding: 6px 12px;
  border-radius: 3px;
  min-width: 75px;
  text-align: center;
  border: 2px solid;
  border-color: ${({ theme, isOrder }) =>
    isOrder ? theme.errorDarken : theme.successDarken};
  color: ${({ theme, isOrder }) =>
    isOrder ? theme.errorDarken : theme.successDarken};
`;

const EventId = styled.h1`
  font-size: 24px;
  margin: 0;
  min-width: 60px;
`;

const EventCard = ({ event, withoutOrderBlock, canDelete }) => {
  const { isUnderTablet } = useScreenType();
  const type = `${ucfirst(event.type)}s`;
  const [deleteAlert, { loading: deleting }] = useMutation(DELETE_ALERT, {
    refetchQueries: [
      `getPending${type}`,
      `getExecuted${type}`,
      'getEvents',
      'getObservers',
    ],
    variables: { id: event.id },
  });

  const onDeleteAlert = () =>
    window.confirm('This action cannot be undo. Are you sure to continue?') &&
    deleteAlert();
  return (
    <CardContainer>
      <FlexCardContainer>
        <FlexInfoContainer>
          <EventId>#{event.id}</EventId>
          <EventType isOrder={event.type === 'order'}>
            {ucfirst(event.type)}
          </EventType>
          <div>
            <div>
              <strong>{`${event.observer.baseAsset}/${event.observer.quoteAsset}`}</strong>
            </div>
            <div>
              <strong>{event.limitDirection}</strong> - limit:{' '}
              <strong>
                {event.lim} {event.observer.quoteAsset}
              </strong>
            </div>
          </div>
          {event.type === 'order' && !isUnderTablet && !withoutOrderBlock && (
            <OrderBlock
              status={event.status}
              orderOptions={event.orderOptions}
              info={event.info}
            />
          )}
        </FlexInfoContainer>
        <div className={canDelete && 'card-actions'}>
          {event.status === 'pending' && (
            <Icon icon='processing' color='warningDarken' />
          )}
          {event.status === 'executed' && (
            <Tooltip
              title={`Executed at ${dayjs(event.executedAt).format(
                'DD/MM/YYYY HH:mm:ss'
              )}`}
            >
              <Icon icon='validCircle' color='successDarken' />
            </Tooltip>
          )}
          {event.status === 'failed' && (
            <Tooltip
              title={`Failed at ${dayjs(event.executedAt).format(
                'DD/MM/YYYY HH:mm:ss'
              )}`}
            >
              <Icon icon='failed' color='errorDarken' />
            </Tooltip>
          )}
        </div>
        {canDelete && (
          <div className='card-delete'>
            <IconButton
              disabled={deleting}
              loading={deleting}
              onClick={onDeleteAlert}
              icon='trash'
              size='sm'
            />
          </div>
        )}
      </FlexCardContainer>
    </CardContainer>
  );
};

export default EventCard;
