import Button from 'components/Buttons/Button';
import Modal, { ModalActions } from 'components/Modal';
import Tabs from 'components/Tabs';
import React from 'react';
import { useState } from 'react';
import ucfirst from 'utils/ucfirst';
import AlertEditor from '../Editor';

import {
  ExecutedList as AlertExecutedList,
  PendingList as AlertPendingList,
} from './AlertList';
import {
  ExecutedList as OrderExecutedList,
  PendingList as OrderPendingList,
} from './OrderList';

const tabs = [
  { label: 'Pending', value: 'pending' },
  { label: 'Executed', value: 'executed' },
];

const computePanes = (isOrder) => [
  {
    Component: isOrder ? OrderPendingList : AlertPendingList,
    value: 'pending',
  },
  {
    Component: isOrder ? OrderExecutedList : AlertExecutedList,
    value: 'executed',
  },
];

const AlertsList = ({ observer, onClose, isOrder }) => {
  const [value, setValue] = useState('pending');
  const panes = computePanes(isOrder);
  const [addAlertModalOpen, setAddAlertModalOpen] = useState(false);

  const onAddAlert = () => setAddAlertModalOpen(true);
  const onCloseAddAlert = () => setAddAlertModalOpen(false);

  return (
    <Modal
      open
      hasCloseButton
      onClose={onClose}
      title={`${ucfirst(value)} ${isOrder ? 'orders' : 'alerts'}`}
      size='md'
    >
      <Tabs
        inModal
        value={value}
        tabs={tabs}
        panes={panes}
        onChange={setValue}
        paneProps={{ observer }}
      />
      <ModalActions>
        <Button onClick={onAddAlert} color='primary' variant='outlined'>
          Add alert
        </Button>
      </ModalActions>
      {addAlertModalOpen && (
        <AlertEditor observer={observer} onClose={onCloseAddAlert} />
      )}
    </Modal>
  );
};

export default AlertsList;
